* {
  box-sizing: border-box;
}
.loader__text {
  color: blueviolet;
}
body {
  margin: 0;
  padding: 0;
  background-color: #eff3f7;
  height: 100%;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
html,
body,
.container {
  width: 100%;
  display: flex;
  justify-content: center;
}
.loader {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.movies {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 40px;
  width: 80%;
  align-items: flex-start;
}
.movies .movie {
  width: 45%;
  background-color: rgb(130, 6, 246);
  margin-bottom: 70px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  color: #fbfbfb;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.15);
}
.movie img {
  position: relative;
  top: -30px;
  width: 100px;
  margin-right: 30px;
}
.movie h3,
h4 {
  font-weight: 400;
  margin: 0;
}
.movie h3 {
  margin-bottom: 7px;
  font-size: 18px;
}

.movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.movie__genres li {
  margin-right: 7px;
}
.movie__anchor {
  background-color: #2b4727;
  text-decoration: none;
  color: rgb(255, 248, 248);
  padding: 7px;
  border-radius: 5px;
}
.movie__anchor:hover {
  opacity: 0.7;
}